
import Loading from 'vux/src/components/loading/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import Lib from 'assets/js/Lib'
let page
let location
let searchNo
export default {
directives: {
TransferDom
},
components: {
Loading,
XButton,
Group,
Cell,
XTable,
Flexbox,
FlexboxItem
},
data () {
return {
kpbz: false,
salesName: '',
buyName: '',
total: '',
date: '',
invoiceCode: '',
invoiceNumber: '',
receiptNumber: '',
orderTime: '',
receiptTotal: '',
ICTotal: '',
showCon: false,
receiptDetail: {
content: []
},
pBottom: '20px',
className: 'btn pb20',
width: '',
show1: false,
text1: '请稍等'
}
},
created () {
page = this
location = window.location.href
const index = location.lastIndexOf('\/')
searchNo = location.substring(index + 1, location.length)
},
mounted () {
Lib.U.setClass()
window.onscroll = function () {
const t = document.documentElement.scrollTop || document.body.scrollTop
if (t > 20) {
page.pBottom = '64px'
page.className = 'btn pb20 fixed'
const w = screen.width
const subW = w - 40
page.width = subW + 'px'
} else {
page.className = 'btn pb20'
page.pBottom = '20px'
}
}
page.show1 = true
Lib.M.ajax({
/* 'type': 'get',*/
'url': '/invoice/orderAndInvoiceSearch?searchNo=' + searchNo,
'success': function (res) {
if (res.code === 0) {
page.salesName = res.data.outstr.gsmc
page.buyName = res.data.outstr.ghfmc
page.total = res.data.outstr.kphjje
page.date = res.data.outstr.kprq
page.invoiceCode = res.data.outstr.fp_DM
page.invoiceNumber = res.data.outstr.fp_HM
page.kpbz = res.data.outstr.kpbz
page.receiptNumber = res.data.fpkjxx_FPTXX.xplsh
page.orderTime = res.data.fpkjxx_FPTXX.xsdate
page.receiptTotal = res.data.fpkjxx_FPTXX.kphjje
page.ICTotal = res.data.fpkjxx_FPTXX.yfkje
page.ICTotal = res.data.fpkjxx_FPTXX.yfkje
page.receiptDetail.content = res.data.fpkjxx_XMXXS.fpkjxx_XMXX
} else {
/* page.errorTxt = res.message*/

}
page.show1 = false
}
})
},
methods: {
clickInvoicePreview () {
window.location.href = '/e-Invoice/invoicePreview.html?searchNo=' + searchNo
},
clickEditTitle () {
window.location.href = '/e-Invoice/invoiceTitle.html?searchNo=' + searchNo
}
}
}




